/* Todo buttons */
.doneTodoButton {
  font-size: 1rem;
  width: 2.2rem;
  height: 2rem;
  background-color: rgb(100, 255, 212);
  border: none;
  color: rgb(120, 120, 120);
  cursor: pointer;
}

.delTodoButton {
  font-size: 1rem;
  width: 2.2rem;
  height: 2rem;
  background-color: rgb(255, 144, 100);
  border: none;
  color: rgb(120, 120, 120);
  cursor: pointer;
}

.editTodoButton {
  font-size: 1rem;
  width: 3rem;
  height: 2rem;
  background-color: rgb(255, 175, 25);
  border: none;
  color: rgb(120, 120, 120);
  cursor: pointer;
}

.todoButtonDisabled {
  font-size: 1rem;
  width: 2.2rem;
  height: 2rem;
  background-color: rgb(230, 230, 230);
  border: none;
  color: rgb(180, 180, 180);
}

.editTodoButtonDisabled {
  font-size: 1rem;
  width: 3rem;
  height: 2rem;
  background-color: rgb(230, 230, 230);
  border: none;
  color: rgb(180, 180, 180);
}

